import * as Phaser from "phaser";
import { AudioManager } from "../audio/AudioManager";

export interface SynthControlConfig {
  x: number;
  y: number;
  width: number;
  height: number;
}

export interface ScreenSize {
  width: number;
  height: number;
  scaleFactor: number;
  isMobile: boolean;
  isTablet: boolean;
  isDesktop: boolean;
}

export class SynthControlPanel {
  private scene: Phaser.Scene;
  private audioManager: AudioManager;
  private config: SynthControlConfig;
  private screenSize: ScreenSize;
  
  // VISUAL COMPONENTS
  private panelBackground!: Phaser.GameObjects.Graphics;
  private titleText!: Phaser.GameObjects.Text;
  
  // LFO CONTROL
  private lfoKnob!: Phaser.GameObjects.Graphics;
  private lfoLabel!: Phaser.GameObjects.Text;
  private lfoValue!: Phaser.GameObjects.Text;
  private lfoPointer!: Phaser.GameObjects.Graphics;
  
  // CHORUS CONTROL
  private chorusKnob!: Phaser.GameObjects.Graphics;
  private chorusLabel!: Phaser.GameObjects.Text;
  private chorusValue!: Phaser.GameObjects.Text;
  private chorusPointer!: Phaser.GameObjects.Graphics;
  
  // DELAY CONTROL (SLIDER)
  private delaySlider!: Phaser.GameObjects.Graphics;
  private delayTrack!: Phaser.GameObjects.Graphics;
  private delayHandle!: Phaser.GameObjects.Graphics;
  private delayLabel!: Phaser.GameObjects.Text;
  private delayValue!: Phaser.GameObjects.Text;
  private delayLEDs!: Phaser.GameObjects.Graphics;
  
  // REVERB CONTROL
  private reverbKnob!: Phaser.GameObjects.Graphics;
  private reverbLabel!: Phaser.GameObjects.Text;
  private reverbValue!: Phaser.GameObjects.Text;
  private reverbPointer!: Phaser.GameObjects.Graphics;
  
  // WAVEFORM SELECTOR
  private waveformSelector!: Phaser.GameObjects.Graphics;
  private waveformLabel!: Phaser.GameObjects.Text;
  private waveformValue!: Phaser.GameObjects.Text;
  private waveformIcons!: Phaser.GameObjects.Text[];
  
  // ANIMATION STATE
  private animationTweens: Phaser.Tweens.Tween[] = [];
  
  constructor(scene: Phaser.Scene, audioManager: AudioManager, config: SynthControlConfig) {
    this.scene = scene;
    this.audioManager = audioManager;
    this.config = config;
    this.screenSize = this.calculateScreenSize();
  }
  
  create(): void {
    this.updateScreenSize();
    this.createPanelBackground();
    this.createTitle();
    this.createLFOControl();
    this.createChorusControl();
    this.createDelayControl();
    this.createReverbControl();
    this.createWaveformSelector();
  }
  
  private createPanelBackground(): void {
    const { x, y, width, height } = this.config;
    
    // MAIN PANEL BACKGROUND
    this.panelBackground = this.scene.add.graphics();
    this.panelBackground.setScrollFactor(0);
    this.panelBackground.setDepth(100);
    
    // DARK SYNTH RACK BACKGROUND
    this.panelBackground.fillStyle(0x2c3e50, 0.95);
    this.panelBackground.fillRoundedRect(x, y, width, height, 8);
    
    // METALLIC BORDER
    this.panelBackground.lineStyle(2, 0x7f8c8d, 1);
    this.panelBackground.strokeRoundedRect(x, y, width, height, 8);
    
    // INNER HIGHLIGHT
    this.panelBackground.lineStyle(1, 0x95a5a6, 0.5);
    this.panelBackground.strokeRoundedRect(x + 1, y + 1, width - 2, height - 2, 6);
  }
  
  private createTitle(): void {
    const { x, y, width } = this.config;
    
    this.titleText = this.scene.add.text(x + width / 2, y + 15, "üéõÔ∏è SYNTH CONTROLS", {
      fontSize: "14px",
      color: "#ecf0f1",
      fontFamily: "Nabla, system-ui",
      stroke: "#000000",
      strokeThickness: 1,
    });
    this.titleText.setOrigin(0.5, 0.5);
    this.titleText.setScrollFactor(0);
    this.titleText.setDepth(101);
  }
  
  private createLFOControl(): void {
    const { x, y } = this.config;
    const knobX = x + 60;
    const knobY = y + 45;
    
    // LFO KNOB BASE
    this.lfoKnob = this.scene.add.graphics();
    this.lfoKnob.setScrollFactor(0);
    this.lfoKnob.setDepth(101);
    
    // KNOB BACKGROUND (YELLOW/AMBER)
    this.lfoKnob.fillStyle(0xf39c12, 0.8);
    this.lfoKnob.fillCircle(knobX, knobY, 20);
    
    // KNOB BORDER
    this.lfoKnob.lineStyle(2, 0xe67e22, 1);
    this.lfoKnob.strokeCircle(knobX, knobY, 20);
    
    // KNOB CENTER
    this.lfoKnob.fillStyle(0x2c3e50, 1);
    this.lfoKnob.fillCircle(knobX, knobY, 8);
    
    // LFO POINTER
    this.lfoPointer = this.scene.add.graphics();
    this.lfoPointer.setScrollFactor(0);
    this.lfoPointer.setDepth(102);
    
    // LABEL
    this.lfoLabel = this.scene.add.text(knobX, knobY + 35, "LFO", {
      fontSize: "10px",
      color: "#f39c12",
      fontFamily: "Nabla, system-ui",
      stroke: "#000000",
      strokeThickness: 1,
    });
    this.lfoLabel.setOrigin(0.5, 0.5);
    this.lfoLabel.setScrollFactor(0);
    this.lfoLabel.setDepth(101);
    
    // VALUE DISPLAY
    this.lfoValue = this.scene.add.text(knobX, knobY + 50, "2.0Hz", {
      fontSize: "8px",
      color: "#ffffff",
      fontFamily: "Nabla, system-ui",
      stroke: "#000000",
      strokeThickness: 1,
    });
    this.lfoValue.setOrigin(0.5, 0.5);
    this.lfoValue.setScrollFactor(0);
    this.lfoValue.setDepth(101);
  }
  
  private createChorusControl(): void {
    const { x, y } = this.config;
    const knobX = x + 140;
    const knobY = y + 45;
    
    // CHORUS KNOB BASE
    this.chorusKnob = this.scene.add.graphics();
    this.chorusKnob.setScrollFactor(0);
    this.chorusKnob.setDepth(101);
    
    // KNOB BACKGROUND (BLUE)
    this.chorusKnob.fillStyle(0x3498db, 0.8);
    this.chorusKnob.fillCircle(knobX, knobY, 20);
    
    // KNOB BORDER
    this.chorusKnob.lineStyle(2, 0x2980b9, 1);
    this.chorusKnob.strokeCircle(knobX, knobY, 20);
    
    // KNOB CENTER
    this.chorusKnob.fillStyle(0x2c3e50, 1);
    this.chorusKnob.fillCircle(knobX, knobY, 8);
    
    // CHORUS POINTER
    this.chorusPointer = this.scene.add.graphics();
    this.chorusPointer.setScrollFactor(0);
    this.chorusPointer.setDepth(102);
    
    // LABEL
    this.chorusLabel = this.scene.add.text(knobX, knobY + 35, "CHORUS", {
      fontSize: "10px",
      color: "#3498db",
      fontFamily: "Nabla, system-ui",
      stroke: "#000000",
      strokeThickness: 1,
    });
    this.chorusLabel.setOrigin(0.5, 0.5);
    this.chorusLabel.setScrollFactor(0);
    this.chorusLabel.setDepth(101);
    
    // VALUE DISPLAY
    this.chorusValue = this.scene.add.text(knobX, knobY + 50, "10%", {
      fontSize: "8px",
      color: "#ffffff",
      fontFamily: "Nabla, system-ui",
      stroke: "#000000",
      strokeThickness: 1,
    });
    this.chorusValue.setOrigin(0.5, 0.5);
    this.chorusValue.setScrollFactor(0);
    this.chorusValue.setDepth(101);
  }
  
  private createDelayControl(): void {
    const { x, y } = this.config;
    const sliderX = x + 220;
    const sliderY = y + 35;
    
    // DELAY SLIDER TRACK
    this.delayTrack = this.scene.add.graphics();
    this.delayTrack.setScrollFactor(0);
    this.delayTrack.setDepth(101);
    
    // SLIDER TRACK BACKGROUND
    this.delayTrack.fillStyle(0x34495e, 1);
    this.delayTrack.fillRoundedRect(sliderX - 3, sliderY, 6, 50, 3);
    
    // SLIDER TRACK BORDER
    this.delayTrack.lineStyle(1, 0x2c3e50, 1);
    this.delayTrack.strokeRoundedRect(sliderX - 3, sliderY, 6, 50, 3);
    
    // DELAY LED STRIP
    this.delayLEDs = this.scene.add.graphics();
    this.delayLEDs.setScrollFactor(0);
    this.delayLEDs.setDepth(101);
    
    // DELAY SLIDER HANDLE
    this.delayHandle = this.scene.add.graphics();
    this.delayHandle.setScrollFactor(0);
    this.delayHandle.setDepth(102);
    
    // LABEL
    this.delayLabel = this.scene.add.text(sliderX, sliderY + 60, "DELAY", {
      fontSize: "10px",
      color: "#2ecc71",
      fontFamily: "Nabla, system-ui",
      stroke: "#000000",
      strokeThickness: 1,
    });
    this.delayLabel.setOrigin(0.5, 0.5);
    this.delayLabel.setScrollFactor(0);
    this.delayLabel.setDepth(101);
    
    // VALUE DISPLAY
    this.delayValue = this.scene.add.text(sliderX, sliderY + 75, "30%", {
      fontSize: "8px",
      color: "#ffffff",
      fontFamily: "Nabla, system-ui",
      stroke: "#000000",
      strokeThickness: 1,
    });
    this.delayValue.setOrigin(0.5, 0.5);
    this.delayValue.setScrollFactor(0);
    this.delayValue.setDepth(101);
  }
  
  private createReverbControl(): void {
    const { x, y } = this.config;
    const knobX = x + 300;
    const knobY = y + 45;
    
    // REVERB KNOB BASE
    this.reverbKnob = this.scene.add.graphics();
    this.reverbKnob.setScrollFactor(0);
    this.reverbKnob.setDepth(101);
    
    // KNOB BACKGROUND (PURPLE)
    this.reverbKnob.fillStyle(0x9b59b6, 0.8);
    this.reverbKnob.fillCircle(knobX, knobY, 20);
    
    // KNOB BORDER
    this.reverbKnob.lineStyle(2, 0x8e44ad, 1);
    this.reverbKnob.strokeCircle(knobX, knobY, 20);
    
    // KNOB CENTER
    this.reverbKnob.fillStyle(0x2c3e50, 1);
    this.reverbKnob.fillCircle(knobX, knobY, 8);
    
    // REVERB POINTER
    this.reverbPointer = this.scene.add.graphics();
    this.reverbPointer.setScrollFactor(0);
    this.reverbPointer.setDepth(102);
    
    // LABEL
    this.reverbLabel = this.scene.add.text(knobX, knobY + 35, "REVERB", {
      fontSize: "10px",
      color: "#9b59b6",
      fontFamily: "Nabla, system-ui",
      stroke: "#000000",
      strokeThickness: 1,
    });
    this.reverbLabel.setOrigin(0.5, 0.5);
    this.reverbLabel.setScrollFactor(0);
    this.reverbLabel.setDepth(101);
    
    // VALUE DISPLAY
    this.reverbValue = this.scene.add.text(knobX, knobY + 50, "30%", {
      fontSize: "8px",
      color: "#ffffff",
      fontFamily: "Nabla, system-ui",
      stroke: "#000000",
      strokeThickness: 1,
    });
    this.reverbValue.setOrigin(0.5, 0.5);
    this.reverbValue.setScrollFactor(0);
    this.reverbValue.setDepth(101);
  }
  
  private createWaveformSelector(): void {
    const { x, y } = this.config;
    const selectorX = x + 380;
    const selectorY = y + 45;
    
    // WAVEFORM SELECTOR BASE
    this.waveformSelector = this.scene.add.graphics();
    this.waveformSelector.setScrollFactor(0);
    this.waveformSelector.setDepth(101);
    
    // SELECTOR BACKGROUND (ORANGE)
    this.waveformSelector.fillStyle(0xf39c12, 0.8);
    this.waveformSelector.fillCircle(selectorX, selectorY, 20);
    
    // SELECTOR BORDER
    this.waveformSelector.lineStyle(2, 0xe67e22, 1);
    this.waveformSelector.strokeCircle(selectorX, selectorY, 20);
    
    // SELECTOR CENTER
    this.waveformSelector.fillStyle(0x2c3e50, 1);
    this.waveformSelector.fillCircle(selectorX, selectorY, 8);
    
    // WAVEFORM ICONS
    this.waveformIcons = [];
    const waveforms = ["‚ö°", "‚¨ú", "‚ñ≥", "‚àø"]; // SAWTOOTH, SQUARE, TRIANGLE, SINE
    const waveformNames = ["SAW", "SQR", "TRI", "SIN"];
    
    waveforms.forEach((icon, index) => {
      const iconText = this.scene.add.text(
        selectorX + (index - 1.5) * 15, 
        selectorY + 25, 
        icon, 
        {
          fontSize: "8px",
          color: "#f39c12",
          fontFamily: "Nabla, system-ui",
        }
      );
      iconText.setOrigin(0.5, 0.5);
      iconText.setScrollFactor(0);
      iconText.setDepth(101);
      this.waveformIcons.push(iconText);
    });
    
    // LABEL
    this.waveformLabel = this.scene.add.text(selectorX, selectorY + 35, "WAVE", {
      fontSize: "10px",
      color: "#f39c12",
      fontFamily: "Nabla, system-ui",
      stroke: "#000000",
      strokeThickness: 1,
    });
    this.waveformLabel.setOrigin(0.5, 0.5);
    this.waveformLabel.setScrollFactor(0);
    this.waveformLabel.setDepth(101);
    
    // VALUE DISPLAY
    this.waveformValue = this.scene.add.text(selectorX, selectorY + 50, "SAW", {
      fontSize: "8px",
      color: "#ffffff",
      fontFamily: "Nabla, system-ui",
      stroke: "#000000",
      strokeThickness: 1,
    });
    this.waveformValue.setOrigin(0.5, 0.5);
    this.waveformValue.setScrollFactor(0);
    this.waveformValue.setDepth(101);
  }
  
  update(): void {
    // GET CURRENT AUDIO PARAMETERS
    const lfoRate = this.audioManager.getLFORate();
    const chorusDepth = this.audioManager.getChorusDepth();
    const delayVolume = this.audioManager.getDelayVolume();
    const reverbAmount = this.audioManager.getReverbAmount();
    const waveform = this.audioManager.getCurrentWaveform();
    
    // UPDATE LFO KNOB
    this.updateLFOKnob(lfoRate);
    
    // UPDATE CHORUS KNOB
    this.updateChorusKnob(chorusDepth);
    
    // UPDATE DELAY SLIDER
    this.updateDelaySlider(delayVolume);
    
    // UPDATE REVERB KNOB
    this.updateReverbKnob(reverbAmount);
    
    // UPDATE WAVEFORM SELECTOR
    this.updateWaveformSelector(waveform);
  }
  
  private updateLFOKnob(rate: number): void {
    const { x, y } = this.config;
    const knobX = x + 60;
    const knobY = y + 45;
    
    // CALCULATE ROTATION ANGLE (0-360 degrees)
    const angle = (rate / 10) * 360; // 0-10 Hz maps to 0-360 degrees
    const radians = (angle - 90) * (Math.PI / 180); // -90 to start at top
    
    // UPDATE POINTER
    this.lfoPointer.clear();
    this.lfoPointer.lineStyle(3, 0xffffff, 1);
    this.lfoPointer.beginPath();
    this.lfoPointer.moveTo(knobX, knobY);
    this.lfoPointer.lineTo(
      knobX + Math.cos(radians) * 15,
      knobY + Math.sin(radians) * 15
    );
    this.lfoPointer.strokePath();
    
    // UPDATE VALUE DISPLAY
    this.lfoValue.setText(`${rate.toFixed(1)}Hz`);
  }
  
  private updateChorusKnob(depth: number): void {
    const { x, y } = this.config;
    const knobX = x + 140;
    const knobY = y + 45;
    
    // CALCULATE ROTATION ANGLE (0-360 degrees)
    const angle = depth * 360; // 0-1 maps to 0-360 degrees
    const radians = (angle - 90) * (Math.PI / 180); // -90 to start at top
    
    // UPDATE POINTER
    this.chorusPointer.clear();
    this.chorusPointer.lineStyle(3, 0xffffff, 1);
    this.chorusPointer.beginPath();
    this.chorusPointer.moveTo(knobX, knobY);
    this.chorusPointer.lineTo(
      knobX + Math.cos(radians) * 15,
      knobY + Math.sin(radians) * 15
    );
    this.chorusPointer.strokePath();
    
    // UPDATE VALUE DISPLAY
    this.chorusValue.setText(`${(depth * 100).toFixed(0)}%`);
  }
  
  private updateDelaySlider(volume: number): void {
    const { x, y } = this.config;
    const sliderX = x + 220;
    const sliderY = y + 35;
    
    // CALCULATE HANDLE POSITION (0-1 maps to slider range)
    const handleY = sliderY + (1 - volume) * 50; // Invert for visual (top = 100%)
    
    // UPDATE HANDLE
    this.delayHandle.clear();
    this.delayHandle.fillStyle(0x2ecc71, 1);
    this.delayHandle.fillRoundedRect(sliderX - 6, handleY - 3, 12, 6, 3);
    this.delayHandle.lineStyle(1, 0x27ae60, 1);
    this.delayHandle.strokeRoundedRect(sliderX - 6, handleY - 3, 12, 6, 3);
    
    // UPDATE LED STRIP
    this.delayLEDs.clear();
    const ledCount = Math.floor(volume * 10); // 0-10 LEDs
    for (let i = 0; i < ledCount; i++) {
      const ledY = sliderY + 5 + i * 4;
      this.delayLEDs.fillStyle(0x2ecc71, 0.8);
      this.delayLEDs.fillRect(sliderX - 2, ledY, 4, 2);
    }
    
    // UPDATE VALUE DISPLAY
    this.delayValue.setText(`${(volume * 100).toFixed(0)}%`);
  }
  
  private updateReverbKnob(amount: number): void {
    const { x, y } = this.config;
    const knobX = x + 300;
    const knobY = y + 45;
    
    // CALCULATE ROTATION ANGLE (0-360 degrees)
    const angle = amount * 360; // 0-1 maps to 0-360 degrees
    const radians = (angle - 90) * (Math.PI / 180); // -90 to start at top
    
    // UPDATE POINTER
    this.reverbPointer.clear();
    this.reverbPointer.lineStyle(3, 0xffffff, 1);
    this.reverbPointer.beginPath();
    this.reverbPointer.moveTo(knobX, knobY);
    this.reverbPointer.lineTo(
      knobX + Math.cos(radians) * 15,
      knobY + Math.sin(radians) * 15
    );
    this.reverbPointer.strokePath();
    
    // UPDATE VALUE DISPLAY
    this.reverbValue.setText(`${(amount * 100).toFixed(0)}%`);
  }
  
  private updateWaveformSelector(waveform: string): void {
    const waveforms = ["sawtooth", "square", "triangle", "sine"];
    const waveformNames = ["SAW", "SQR", "TRI", "SIN"];
    const currentIndex = waveforms.indexOf(waveform);
    
    // UPDATE VALUE DISPLAY
    if (currentIndex >= 0) {
      this.waveformValue.setText(waveformNames[currentIndex]);
    }
    
    // HIGHLIGHT CURRENT WAVEFORM ICON
    this.waveformIcons.forEach((icon, index) => {
      if (index === currentIndex) {
        icon.setColor("#ffffff");
        icon.setScale(1.2);
      } else {
        icon.setColor("#f39c12");
        icon.setScale(1.0);
      }
    });
  }
  
  destroy(): void {
    // CLEAN UP ALL GRAPHICS OBJECTS
    this.panelBackground?.destroy();
    this.titleText?.destroy();
    this.lfoKnob?.destroy();
    this.lfoLabel?.destroy();
    this.lfoValue?.destroy();
    this.lfoPointer?.destroy();
    this.chorusKnob?.destroy();
    this.chorusLabel?.destroy();
    this.chorusValue?.destroy();
    this.chorusPointer?.destroy();
    this.delaySlider?.destroy();
    this.delayTrack?.destroy();
    this.delayHandle?.destroy();
    this.delayLabel?.destroy();
    this.delayValue?.destroy();
    this.delayLEDs?.destroy();
    this.reverbKnob?.destroy();
    this.reverbLabel?.destroy();
    this.reverbValue?.destroy();
    this.reverbPointer?.destroy();
    this.waveformSelector?.destroy();
    this.waveformLabel?.destroy();
    this.waveformValue?.destroy();
    this.waveformIcons?.forEach(icon => icon.destroy());
    
    // CLEAN UP ANIMATIONS
    this.animationTweens.forEach(tween => tween.destroy());
    this.animationTweens = [];
  }
  
  // RESPONSIVE DESIGN METHODS
  private calculateScreenSize(): ScreenSize {
    const { width, height } = this.scene.scale;
    const scaleFactor = Math.min(Math.min(width / 1024, height / 768), 1);
    
    return {
      width,
      height,
      scaleFactor,
      isMobile: width < 768,
      isTablet: width >= 768 && width < 1024,
      isDesktop: width >= 1024
    };
  }
  
  private updateScreenSize(): void {
    this.screenSize = this.calculateScreenSize();
  }
  
  updateLayout(): void {
    this.updateScreenSize();
    this.updatePanelLayout();
    this.updateControlLayouts();
  }
  
  private updatePanelLayout(): void {
    const { width, height } = this.screenSize;
    const { scaleFactor, isMobile, isTablet } = this.screenSize;
    
    // ADJUST PANEL SIZE BASED ON SCREEN SIZE
    let panelWidth, panelHeight, panelX, panelY;
    
    if (isMobile) {
      // MOBILE: COMPACT HORIZONTAL LAYOUT
      panelWidth = Math.min(width - 24, 380);
      panelHeight = 70;
      panelX = 12;
      panelY = 12;
    } else if (isTablet) {
      // TABLET: MEDIUM LAYOUT
      panelWidth = Math.min(width - 24, 580);
      panelHeight = 90;
      panelX = 12;
      panelY = 12;
    } else {
      // DESKTOP: FULL LAYOUT
      panelWidth = Math.min(width - 24, 520);
      panelHeight = 110;
      panelX = 12;
      panelY = 50;
    }
    
    // UPDATE CONFIG
    this.config = {
      x: panelX,
      y: panelY,
      width: panelWidth,
      height: panelHeight
    };
    
    // UPDATE PANEL BACKGROUND
    if (this.panelBackground) {
      this.panelBackground.clear();
      this.panelBackground.fillStyle(0x2c3e50, 0.95);
      this.panelBackground.fillRoundedRect(panelX, panelY, panelWidth, panelHeight, 8);
      this.panelBackground.lineStyle(2, 0x7f8c8d, 1);
      this.panelBackground.strokeRoundedRect(panelX, panelY, panelWidth, panelHeight, 8);
      this.panelBackground.lineStyle(1, 0x95a5a6, 0.5);
      this.panelBackground.strokeRoundedRect(panelX + 1, panelY + 1, panelWidth - 2, panelHeight - 2, 6);
    }
    
    // UPDATE TITLE
    if (this.titleText) {
      this.titleText.setPosition(panelX + panelWidth / 2, panelY + 15);
      this.titleText.setScale(scaleFactor);
    }
  }
  
  private updateControlLayouts(): void {
    const { scaleFactor, isMobile, isTablet } = this.screenSize;
    const { x, y, width, height } = this.config;
    
    // CALCULATE CONTROL POSITIONS BASED ON SCREEN SIZE
    let controlSpacing, controlY, knobSize, fontSize, labelFontSize, valueOffset;
    
    if (isMobile) {
      // MOBILE: COMPACT LAYOUT
      controlSpacing = (width - 40) / 5; // 5 controls with padding
      controlY = y + 30;
      knobSize = 14;
      fontSize = 7;
      labelFontSize = 9;
      valueOffset = 20;
    } else if (isTablet) {
      // TABLET: MEDIUM LAYOUT
      controlSpacing = (width - 60) / 5;
      controlY = y + 40;
      knobSize = 18;
      fontSize = 8;
      labelFontSize = 10;
      valueOffset = 25;
    } else {
      // DESKTOP: FULL LAYOUT
      controlSpacing = 90;
      controlY = y + 50;
      knobSize = 22;
      fontSize = 9;
      labelFontSize = 11;
      valueOffset = 30;
    }
    
    // UPDATE LFO CONTROL
    this.updateControlLayout('lfo', x + 20 + controlSpacing * 0, controlY, knobSize, fontSize, labelFontSize, scaleFactor, valueOffset);
    
    // UPDATE CHORUS CONTROL
    this.updateControlLayout('chorus', x + 20 + controlSpacing * 1, controlY, knobSize, fontSize, labelFontSize, scaleFactor, valueOffset);
    
    // UPDATE DELAY CONTROL
    this.updateControlLayout('delay', x + 20 + controlSpacing * 2, controlY, knobSize, fontSize, labelFontSize, scaleFactor, valueOffset);
    
    // UPDATE REVERB CONTROL
    this.updateControlLayout('reverb', x + 20 + controlSpacing * 3, controlY, knobSize, fontSize, labelFontSize, scaleFactor, valueOffset);
    
    // UPDATE WAVEFORM SELECTOR
    this.updateControlLayout('waveform', x + 20 + controlSpacing * 4, controlY, knobSize, fontSize, labelFontSize, scaleFactor, valueOffset);
  }
  
  private updateControlLayout(controlType: string, x: number, y: number, knobSize: number, fontSize: number, labelFontSize: number, scaleFactor: number, valueOffset: number): void {
    // UPDATE KNOB SIZE AND POSITION
    const knob = this.getControlKnob(controlType);
    if (knob) {
      knob.clear();
      this.drawKnob(knob, x, y, knobSize, controlType);
    }
    
    // UPDATE LABEL
    const label = this.getControlLabel(controlType);
    if (label) {
      label.setPosition(x, y + knobSize + 12);
      label.setFontSize(`${labelFontSize}px`);
      label.setScale(scaleFactor);
    }
    
    // UPDATE VALUE DISPLAY
    const value = this.getControlValue(controlType);
    if (value) {
      value.setPosition(x, y + knobSize + valueOffset);
      value.setFontSize(`${fontSize}px`);
      value.setScale(scaleFactor);
    }
    
    // UPDATE POINTER (for knobs)
    if (controlType !== 'delay' && controlType !== 'waveform') {
      const pointer = this.getControlPointer(controlType);
      if (pointer) {
        // Pointer will be updated in the main update method
      }
    }
  }
  
  private drawKnob(graphics: Phaser.GameObjects.Graphics, x: number, y: number, size: number, controlType: string): void {
    const colors = {
      lfo: { fill: 0xf39c12, stroke: 0xe67e22 },
      chorus: { fill: 0x3498db, stroke: 0x2980b9 },
      delay: { fill: 0x2ecc71, stroke: 0x27ae60 },
      reverb: { fill: 0x9b59b6, stroke: 0x8e44ad },
      waveform: { fill: 0xf39c12, stroke: 0xe67e22 }
    };
    
    const color = colors[controlType as keyof typeof colors];
    
    // KNOB BACKGROUND
    graphics.fillStyle(color.fill, 0.8);
    graphics.fillCircle(x, y, size);
    
    // KNOB BORDER
    graphics.lineStyle(2, color.stroke, 1);
    graphics.strokeCircle(x, y, size);
    
    // KNOB CENTER
    graphics.fillStyle(0x2c3e50, 1);
    graphics.fillCircle(x, y, size * 0.4);
  }
  
  private getControlKnob(controlType: string): Phaser.GameObjects.Graphics | null {
    switch (controlType) {
      case 'lfo': return this.lfoKnob;
      case 'chorus': return this.chorusKnob;
      case 'delay': return this.delayTrack;
      case 'reverb': return this.reverbKnob;
      case 'waveform': return this.waveformSelector;
      default: return null;
    }
  }
  
  private getControlLabel(controlType: string): Phaser.GameObjects.Text | null {
    switch (controlType) {
      case 'lfo': return this.lfoLabel;
      case 'chorus': return this.chorusLabel;
      case 'delay': return this.delayLabel;
      case 'reverb': return this.reverbLabel;
      case 'waveform': return this.waveformLabel;
      default: return null;
    }
  }
  
  private getControlValue(controlType: string): Phaser.GameObjects.Text | null {
    switch (controlType) {
      case 'lfo': return this.lfoValue;
      case 'chorus': return this.chorusValue;
      case 'delay': return this.delayValue;
      case 'reverb': return this.reverbValue;
      case 'waveform': return this.waveformValue;
      default: return null;
    }
  }
  
  private getControlPointer(controlType: string): Phaser.GameObjects.Graphics | null {
    switch (controlType) {
      case 'lfo': return this.lfoPointer;
      case 'chorus': return this.chorusPointer;
      case 'reverb': return this.reverbPointer;
      default: return null;
    }
  }
}
